.nav-menu {
  display: flex;
  align-items: center;
  height: fit-content;
  background: black;
  justify-content: space-around;
  height: 3.5rem;
}

.nav-menu a {
  display: block;
  padding: 0.5rem;
  height: 100%;
  color: white;
}
.nav-menu a:hover {
  text-decoration: none;
  cursor: pointer;
  color: orange;
}
.nav-primary {
  /* flex: 1.5;
  margin-left: 2rem; */
  display: flex;
}

.nav-user-cart {
  display: flex;
  align-items: center;
}
.nav-user {
  display: flex;
  align-items: center;
}
.nav-cart {
  position: relative;
  padding: 2rem;
}
.nav-cart .counter {
  position: absolute;
  top: 20px;
  right: 8px;
  width: 1.5rem;
  text-align: center;
  height: 1.5rem;
  z-index: 3;

  border-radius: 50%;
  background-color: red;
  color: white;
}

.nav-user span {
  padding: 0;
  margin-left: 0.5rem;
}
.nav-menu .faicon {
  color: white;
}
.nav-menu .faicon:hover {
  color: orange;
}
.nav-menu .active {
  color: orange;
}
